.RecommendedVideos
{
    flex: 0.8;
    background-color: #f9f9f9;
    padding-bottom: 0;
    padding: 40px 20px;
}
.videoCard
{
    width: 270px;
    margin-bottom: 40px;

}
.videoCard__thumbnail
{
    height: 140px;
    width: 250px;
    
}
.RecommendedVideos__Videos
{
    display: flex;
    flex-wrap: wrap;
}
.videoCard__info
{
    display: flex;
    padding-right: 30px;
    margin-top: 10px;
}
.videoCard__text
{
    margin-left: 15px;
}
.videoCard__text > h4
{
    font-size: 14px;
    margin-bottom: 5px;
}
.videoCard__text > p
{
    font-size: 14px;
    color: gray;    
}
.videCard__avatar
{
    height: 30px !important;
    width: 30px !important;
}