.channelRow
{
    display: flex;
    color: black;
    align-items: center;
    width: 70%;
}
.channelRow__text
{
    display: flex;
    flex-direction: column;

}
.channelRow__text > p
{
    color: #606060;
    font-size: small !important;
}
.channelRow__logo
{
    height: 120px !important;
    width: 120px !important;
    margin: 10px 60px;
}
.channelRow__text > h4
{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.channelRow > hr
{
    color: gray;
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}